.picture_container {
    width: 100%;
    height: 100%;
}

.picture {
    width: 100%;
    height: 100%;

    border-radius: 50%;

    object-fit: fill;
}


.skeleton {
    animation: skeleton-loading 1s linear infinite alternate;
}

@keyframes skeleton-loading {
    0% {
        background-color: hsl(200, 20%,70%);
    }

    100% {
        background-color: hsl(200, 20%,85%);
    }
}